/*== start of code for tooltips ==*/
.tool {
  cursor: help;
  position: relative;
  color: #17b06b;
}

/*== common styles for both parts of tool tip ==*/
.tool::before,
.tool::after {
  left: 50%;
  opacity: 0;
  position: absolute;
  z-index: -100;
}

.tool:hover::before,
.tool:focus::before,
.tool:hover::after,
.tool:focus::after {
  opacity: 1;
  transform: scale(1) translateY(0);
  z-index: 100;
}

/*== pointer tip ==*/
.tool::before {
  border-style: dashed;
  border-width: 1em 0.75em 0 0.75em;
  border-color: #17b06b transparent transparent transparent;
  bottom: 100%;
  content: "";
  margin-left: -0.5em;
  transition: all 0.65s cubic-bezier(0.84, -0.18, 0.31, 1.26),
    opacity 0.65s 0.5s;
  transform: scale(0.6) translateY(-90%);
}

.tool:hover::before,
.tool:focus::before {
  transition: all 0.65s cubic-bezier(0.84, -0.18, 0.31, 1.26) 0.2s;
}

/*== speech bubble ==*/
.tool::after {
  background: #000;
  border: 3px dashed #17b06b;
  bottom: 180%;
  color: #edeff0;
  content: attr(data-tip);
  white-space: pre-wrap;
  margin-left: -8.75em;
  padding: 1em;
  transition: all 0.65s cubic-bezier(0.84, -0.18, 0.31, 1.26) 0.2s;
  transform: scale(0.6) translateY(50%);
  width: 28.5em;
}

.tool:hover::after,
.tool:focus::after {
  transition: all 0.65s cubic-bezier(0.84, -0.18, 0.31, 1.26);
}

@media (max-width: 760px) {
  .App {
    margin-left: auto;
    margin-right: auto;
    padding-left: 30px;
    padding-right: 30px;
  }
  .tool::after {
    font-size: 0.75em;
    margin-left: -5em;
    width: 10em;
  }
}

.outer-circle {
  background-color: #ffffff88;
  width: 56px;
  height: 56px;
  border-radius: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;

  animation: outer-animation 1s infinite ease-in-out;
  animation-delay: 1s;
}

.inner-circle {
  background-color: #ffffff;
  width: 28px;
  height: 28px;
  border-radius: 28px;
  animation: inner-animation 1s infinite ease-in-out;
}
@keyframes outer-animation {
  0%,
  100% {
    width: 56px;
    height: 56px;
  }

  50% {
    width: 28px;
    height: 28px;
  }
}
@keyframes inner-animation {
  0%,
  100% {
    transform: scale(0);
  }

  50% {
    transform: scale(1);
  }
}
